/**
 * Application constants and configuration values
 */

import { homedir } from "node:os";
import { join } from "node:path";

// Base directories
export const CLAUDE_ZEST_DIR = join(homedir(), ".claude-zest");
export const QUEUE_DIR = join(CLAUDE_ZEST_DIR, "queue");
export const LOGS_DIR = join(CLAUDE_ZEST_DIR, "logs");
export const STATE_DIR = join(CLAUDE_ZEST_DIR, "state");

// File paths
export const SESSION_FILE = join(CLAUDE_ZEST_DIR, "session.json");
export const CONFIG_FILE = join(CLAUDE_ZEST_DIR, "config.json");
export const LOG_FILE = join(LOGS_DIR, "plugin.log");
export const SYNC_LOG_FILE = join(LOGS_DIR, "sync.log");
export const DAEMON_PID_FILE = join(CLAUDE_ZEST_DIR, "daemon.pid");

// Queue files
export const EVENTS_QUEUE_FILE = join(QUEUE_DIR, "events.jsonl");
export const SESSIONS_QUEUE_FILE = join(QUEUE_DIR, "chat-sessions.jsonl");
export const MESSAGES_QUEUE_FILE = join(QUEUE_DIR, "chat-messages.jsonl");

// Platform and source identifiers
export const PLATFORM = "terminal";
export const SOURCE = "claude-code";
export const CLIENT_ID = "claude-cli";

// Sync configuration
export const SYNC_INTERVAL_MS = 60000; // 60 seconds
export const MAX_RETRY_ATTEMPTS = 3;
export const RETRY_BACKOFF_MS = 5000;

// Authentication configuration
export const PROACTIVE_REFRESH_THRESHOLD_MS = 5 * 60 * 1000; // Refresh tokens 5 minutes before expiration

// Content size limits
export const MAX_DIFF_SIZE_BYTES = 10 * 1024 * 1024; // 10MB
export const MAX_CONTENT_PREVIEW_LENGTH = 1000; // characters
export const MAX_SESSION_TITLE_LENGTH = 100; // characters
export const MIN_SESSION_TITLE_LENGTH = 3; // characters

// Session quality filters
export const MIN_MESSAGES_PER_SESSION = 3; // Minimum messages required to upload a session
export const STALE_SESSION_AGE_MS = 7 * 24 * 60 * 60 * 1000; // 7 days in milliseconds

// API endpoints (configured from .env at build time)
// Values are baked into the bundle during build
export const WEB_APP_URL = process.env.ZEST_WEB_APP_URL || "http://localhost:3000";
export const SUPABASE_URL = process.env.ZEST_SUPABASE_URL || "";
export const SUPABASE_ANON_KEY = process.env.ZEST_SUPABASE_ANON_KEY || "";

// Claude project directories
export const CLAUDE_PROJECTS_DIR = join(homedir(), ".claude", "projects");
